      h2(data-aos="fade-left") 2.3 Anlisis de actores involucrados

    .row.my-4(data-aos="fade-left")
      p La herramienta de partida del EML se denomina <strong>mapa de involucrados</strong>. Este es un anlisis simple donde se registra el inters de los actores en el proyecto y sus implicaciones, pero existen herramientas ms elaboradas como puede ser una <strong>matriz de expectativa-fuerza</strong> para contemplar los intereses, percepciones, expectativas y el grado de influencia de personas, grupos, comunidades o entidades; esto es muy importante porque permite por un lado optimizar los recursos y los beneficios del proyecto, adems de potenciar el alcance, la eficacia y el impacto, y por otro lado, prevenir y mitigar los efectos negativos de o hacia otros actores contradictores o afectados, e incluso abordar conflictos de tal manera que el proyecto aporte a su solucin.

    .row.my-4
      p.text-center De acuerdo con lo propuesto por <strong>Ortegn et.al (2005)</strong>, el anlisis debe proceder a:

    TabsA.color-secundario
      .tarjeta.p-4.color-tarjeta(titulo="1. Visibilizar a los <br> involucrados")
        .row.bgr-verde-claro.p-4.bordes-redondeados
          .col-lg-7
            p.my-5 Identificar a todos los actores que pudiesen tener una relacin directa o indirecta con el proyecto a alguna de sus implicaciones.
          .col-lg-5
            figure.my-5
              img(data-aos="zoom-in",src='@/assets/curso/temas/tema2/img33.svg', alt='Imagen decorativa',width="150px",height="150px")
      .tarjeta.p-4.color-tarjeta(titulo="2. Categorizar a los <br> involucrados")
        .row.bgr-verde-claro.p-4.bordes-redondeados
          .col-lg-7.mb-4
            p.my-5 Indagar sobre su entorno, roles, intereses, grado de influencia o capacidad de integracin al proyecto, etc. Aqu puede ser necesario el contacto directo.
          .col-lg-5
            figure.my-5
              img(data-aos="zoom-in",src='@/assets/curso/temas/tema2/img34.svg', alt='Imagen decorativa',width="150px",height="150px")
      .tarjeta.p-4.color-tarjeta(titulo="3. Valorar sus <br> intereses")
        .row.bgr-verde-claro.p-4.bordes-redondeados
          .col-lg-7.mb-4
            p.my-5 Identificar sus percepciones, su actitud y/o posicin frente al proyecto, los conflictos existentes y posibles y los recursos polticos, legales, humanos y financieros que puedan aportar a la solucin de los problemas. En conclusin, identificar oportunidades y amenazas.
          .col-lg-5
            figure.my-5
              img(data-aos="fade-left")(src='@/assets/curso/temas/tema2/img35.svg', alt='Imagen decorativa',width="150px",height="150px")
      .tarjeta.p-4.color-tarjeta(titulo="4. Disear estrategias de <br> participacin")
        .row.bgr-verde-claro.p-4.bordes-redondeados
          .col-lg-7.mb-4
            p.my-5 Analizar e interpretar la informacin para concluir acerca de la participacin en el proyecto o de su modificacin dado el caso.
          .col-lg-5
            figure.my-5
              img(data-aos="zoom-in",src='@/assets/curso/temas/tema2/img36.svg', alt='Imagen decorativa',width="150px",height="150px")
    
