      .col-lg-8.mb-2
        .cajon.color-acento-botones.p-4(data-aos="slide-down",style="background: #FFD94715")
          p.mt-0 Muchas fuentes de informacin acerca del mtodo EML usan un ejemplo recurrente de aplicacin de la herramienta de <strong>lluvia de ideas</strong> (<em>brainstorming</em>) con el protagonismo de la misma poblacin que es perfectamente vlido pero que se puede fortalecer por medio de la indagacin del contexto de la problemtica identificada, previamente al ejercicio de definicin del problema central.
        
        p.mt-3 La acertada formulacin del proyecto y el mismo xito de la intervencin dependen en gran medida de la adecuada identificacin del problema, es por esto que se deben dedicar <strong>esfuerzos proporcionales en la etapa de diagnstico.</strong> El principal referente de este documento, Ortegn et.al (2005), aclara que la planificacin nace de <strong>la percepcin que se tiene de la problemtica</strong> y que se puede dar en concordancia con los siguientes aspectos:
    
    
    .tarjeta--container.row.mb-5
      .col-md.tarjeta.color-acento-botones.p-5(data-aos='fade-down', style='background: #E1EDB5')
        .row.justify-content-center.mb-4
          .col-lg-10.m-auto
            figure
              img(src='@/assets/curso/temas/tema2/img39.svg',  alt='Muestra los Aspectos para identificar problemticas, segn: La aplicacin de una poltica de desarrollo.  Ej. Agenda 2030  o el Plan Nacional. Recuperacin de infraestructura. Necesidades o carencias en la poblacin afectada. Bajos niveles de desarrollo. Condiciones de vida deficitarias. Efectos de la globalizacin de la economa y de tratados comerciales internacionales. ' style='max-width:170px; max-height:170px; margin: auto')
          
        p.text-center La aplicacin de una poltica de desarrollo.  Ej. Agenda 2030 o el Plan Nacional
      .col-md.tarjeta.color-acento-botones.p-5(data-aos='fade-down', style='background: #FFFAE3')
