    row(data-aos="fade-up")
      div.p-4(style="background: #EFF3F6")
        p.my-4.text-bold.text-center Adems, en la actualidad podemos identificar circunstancias problemticas en torno a:
        ul.lista-ul.px-5
          li 
            i.lista-ul__vineta(style="color: black") 
            | <strong>Impactos ambientales adversos </strong> de las actividades productivas en todos los sectores de la economa.
          li 
            i.lista-ul__vineta(style="color: black") 
            | <strong>Efectos directos o indirectos </strong> del conflicto armado o del dominio territorial de grupos armados.
          li 
            i.lista-ul__vineta(style="color: black") 
            | <strong>Efectos regresivos</strong> de polticas de desarrollo o coyunturales.
          li 
            i.lista-ul__vineta(style="color: black") 
            | <strong>Consecuencias socioeconmicas</strong> derivadas de la pandemia por el virus Sars-COV-2.
          li 
            i.lista-ul__vineta(style="color: black") 
            | <strong>Aspectos inherentes a los efectos acumulados por dcadas</strong> en cuanto a la concentracin de la riqueza y la tierra, la pauperizacin del campesinado, el envejecimiento de la poblacin rural, la migracin masiva hacia las urbes, el deterioro del tejido social en la ruralidad, el despojo de la tierra, la capacidad productiva, los derechos y hasta los valores de la cultura rural, el desvanecimiento del relevo y empalme generacional, el rezago educativo para la poblacin rural, el escaso acceso a los factores productivos y la tecnologa para la productividad, la ausencia del estado y los programas sociales de los gobiernos en especial en la periferia, la inseguridad alimentaria y el hambre y malnutricin derivados de sta, la inequidad de gnero, el deterioro del suelo aprovechable, los efectos del cambio climtico sobre los ecosistemas por causas antrpicas, la huella de carbono generada por actividades como la ganadera extensiva, la contaminacin de fuentes de agua, la ampliacin de la frontera agrcola hacia zonas de reserva protegidas, el debilitamiento de la ciudadana participativa, la lentitud en la migracin hacia fuentes de energa renovables, la escasa infraestructura productiva para las actividades agropecuarias, entre otros.

