    #t_2_5.titulo-segundo.color-acento-contenido
      h2(data-aos="fade-left") 2.5 Identificacin de problemas a partir de informacin primaria

    .row.align-items-center(data-aos="fade-left")
      .col-lg-8.my-2.mb-3
        p La identificacin de problemas por parte de la misma comunidad afectada no solo es importante para la adecuada formulacin de un plan de intervencin exitoso, <strong>la participacin de la poblacin en el proceso de desarrollo es importante para la sostenibilidad de los procesos de mejora en las actividades productivas</strong>, ya que muchas veces el fracaso de los proyectos se atribuye a la falta de sentido de pertenencia de las comunidades con estos, porque algunas veces se hacen intervenciones sin contar siquiera con su opinin al respecto.
        .cajon.color-acento-contenido.p-4(data-aos="slide-down",style="background: #FFE9E1")
          p.mt-0 El paradigma del desarrollo desde abajo viene a contrarrestar las polticas de intervencin desde los dirigentes nacionales y grandes actores internacionales <strong>(BM, FMI, tesoro nacional de USA, etc.)</strong> que a partir de los aos 80 vienen fomentando la aplicacin de polticas derivadas de la receta neoliberal (menor injerencia del estado y mayor orientacin del mercado en las dinmicas socioeconmicas) producto del llamado consenso de Washington, con un <strong>rotundo fracaso en trminos de impacto ambiental y aumento de la brecha social a escala continental.</strong>  
      .col-lg-4.my-2
        figure
          img(src='@/assets/curso/temas/tema2/img45.png',  alt='')

    p.my-4(data-aos="fade-left") El extensionismo agropecuario, actividad que data del <strong>siglo XIX en Estados Unidos y Europa como una iniciativa de atencin a la ruralidad desde los gobiernos con el apoyo del sector privado</strong>, especialmente el productor de tecnologa, ha logrado avances muy interesantes y exitosos en el diseo y ejecucin de procesos de intervencin integrales ms que asistenciales. Uno de los casos destacados en Colombia y la regin es la <strong>Federacin Nacional de Cafeteros.</strong> Producto de estas experiencias fortalecidas se encuentran metodologas centradas en la participacin de la poblacin, como el <strong>Diagnstico Rural Participativo (DRP)</strong> y la planificacin comunitaria, entre otros ejercicios como los realizados por el <strong>Servicio Nacional de Aprendizaje (SENA)</strong> en sus modelos de intervencin hechos hasta la dcada de los aos 90 (por efectos de las polticas coyunturales) a partir de los programas <strong>Capacitacin para la Participacin Campesina (CAPACA),  capacitacin empresarial campesina, la capacitacin para la organizacin y participacin comunitaria, programa de extensin rural del convenio SENA-Gobierno de Holanda</strong> y el trabajo  <strong>SENA-Escuela Nacional de Formacin Campesina ANUC-ENFOCA</strong>, estrategias con productos que se pueden consultar en los repositorios institucionales. 

  
    figure(data-aos="fade-up")
      .video
        iframe(width="560" height="315" src="https://www.youtube.com/embed/sGXIL9KjxLQ?si=K62WXo3sF9jq85dm" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen)
      figcaption Lo invitamos a consultar el siguiente video.
       

    .row.align-items-center.my-3(style="background: #EEF5D5")(data-aos="fade-up")
      p.my-4 La informacin de primera mano es muy valiosa y requiere de un recurso metodolgico que puede ser el DRP.
      .col-lg-6
        p Existen otras metodologas, por ejemplo la propuesta hecha por Orlando Fals Borda en 1977, denominada <strong>Investigacin-Accin-Participacin (IAP)</strong>, que centraliza la persona y no el objeto de la investigacin. Otras metodologas pueden enfocarse a grupos pequeos como los familiares, es el caso de la Planificacin Predial Participativa (PPP) o a organizaciones y empresas rurales como el <strong>ndice de Capacidades Organizacionales de la Unin Europea (ICO)</strong> y la <strong>Medicin del Emprendimiento Rural (MER)</strong> de la FAO que incorporan todas las distintas reas que componen una organizacin formal.
      .col-lg-6
        p La gestin de la informacin, en todo caso, debe ser organizada y metdica, siguiendo la ruta datos-informacin-conocimiento; es decir, como un proceso de investigacin bsico y para ello existen diversas herramientas (de las que habla el punto cinco del ciclo de siete pasos del DRP), que pueden aplicarse en la amplitud de las actividades rurales. Algunas son muy verstiles como las entrevistas y las lluvias de ideas, otras ms especficas como los mapas de recursos, las caracterizaciones de las actividades productivas mediante mapeos y los calendarios de produccin, otras ms enfocadas a aspectos de gnero o de comunicacin, sociales u otros. 

