    #t_2_6.titulo-segundo.color-acento-contenido
      h2(data-aos="fade-left") 2.6 Priorizacin de problemas a travs de la herramienta Matriz de Vester
    
    .row.my-3.align-items-center(data-aos="fade-left")
      .col-lg-8
        p El objetivo del proceso de diagnstico es <strong>identificar y definir muy bien una serie de problemas de diversa ndole</strong>, donde una vez colectados, se tienen dos situaciones posibles: que se identifique fcilmente el problema central o que no se tenga claro. En el primer caso, podemos pasar al siguiente punto, el anlisis causal (causa-efecto), donde el EML nos introduce a la tcnica de rboles, la ms adecuada, aunque existan otras formas de anlisis causa-efecto.
        .cajon.color-acento-botones.p-4(data-aos="slide-down",style="background: #FFD94715")
          p.mt-0 En el segundo caso, donde no se tiene definido el problema central, es necesario como siguiente paso <strong>analizar la relacin entre los problemas de forma bidireccional</strong>; es decir, escudriar en la correlacin entre los distintos problemas (aqu variables) a fin de establecer el grado de influencia y/o dependencia entre ellos, el mtodo, desarrollado por el alemn <strong>Frederic Vester (1925-2003)</strong>, se fundamenta en la matemtica (teora de matrices) estableciendo un valor numrico dentro de un rango determinado que normalmente es [0-3] proporcional al grado de influencia a discrecin del analista (0 para una influencia nula o indirecta y 3 para una influencia marcada o directa).

      .col-lg-4(data-aos="fade-right")
        figure
          img(src='@/assets/curso/temas/tema2/img54.png', alt='')

    .row.my-5(data-aos="fade-left")
      .col-lg-12.crd.crd--avatarHorizontal-left-bgr5.py-3.mb-3
        .row.align-items-center
          .col-auto(style="z-index:1")
            figure
              img(src="@/assets/curso/temas/tema2/img55.svg", alt="", width= "100px", height="100px")
          .col(style="z-index:1")
            p.m-0 As, a la relacin entre los problemas, que son cualitativos, se le atribuye <strong>una magnitud y del ordenamiento matricial</strong> de estos valores se puede identificar la variable (problema) con mayor influencia en relacin a las dems. En resumen, la matriz de Vester, tambin llamada matriz de <strong>influencia-dependencia (ID)</strong> nos permite <strong>identificar el problema central y tres grupos ms o menos diferenciados</strong> (causas, consecuencias y supuestos). Una consideracin importante es que la herramienta es eficaz cuando se consideran al menos 13 problemas (mnimo grado en el que adquiere significancia el tratamiento de los datos).
    
    p.text-center.my-5 Antes de proseguir con la descripcin del mtodo paso por paso vale la pena enfatizar en las siguientes aclaraciones a la hora de redactar los problemas:

    hr.m-auto.mb-4(style="background: #FFA686")(width="83%")
    .col-lg-10.m-auto(data-aos="fade-up")
      .bloque-texto-f.color-secundario.pt-4.px-5
        .bloque-texto-f__comillas
          i.fas.fa-quote-left
          p.bloque-texto-f__texto Un error que se presenta frecuentemente en la definicin del problema central, surge cuando este se describe como la falta o ausencia de una solucin frente a una necesidad experimentada por la poblacin. Este hecho sucede frecuentemente con intervenciones que conllevan la construccin de diferentes tipos de infraestructura pblica o la adquisicin de equipos. El definir la situacin problemtica de esta forma, conlleva dos debilidades en la formulacin del proyecto: se limita y condiciona la solucin a esa sola alternativa y se ignoran las condiciones que afectan realmente a la poblacin en cuanto a su bienestar y el mejoramiento de las condiciones de vida.
          h3.bloque-texto-f__texto  (DNP, 2016, p.15)
          i.fas.fa-quote-right

    p.text-center.my-3 <strong>Recomendaciones para la correcta definicin de un problema.</strong>
