      .row(numero="2" titulo="Paso 2")
        .col-lg-12.mb-4.mb-md-0
          p Se procede a la construccin de la matriz <strong>enfrentando los problemas en ambos ejes</strong> como se muestra en la figura. En la interseccin de los problemas contra s mismos, se pone el valor de cero. El problema debe estar redactado de manera <strong>muy precisa.</strong> 
