      .row(numero="3" titulo="Paso 3")
        .col-lg-12.mb-4.mb-md-0
          p Se asigna la puntuacin empezando por <strong>la fila superior de izquierda a derecha</strong>, teniendo en cuenta que esta ponderacin corresponde al grado de influencia del problema de la fila con respecto a cada problema de las columnas. Al establecer las relaciones de causalidad es importante considerar una relacin directa entre los problemas. Una forma de verificar que el ejercicio se est haciendo bien es que al terminar de asignar valores, no ms del 30% de ellos corresponde a un valor de tres (3); caso contrario, existe un sesgo atribuible a la falta de definicin de los problemas o a la consideracin de causas indirectas entre ellos. Se puede hacer esta pregunta cada vez que se confronten los problemas: <strong>Cmo influye el problema 1 sobre el problema 2?; Cmo influye el problema 1 sobre el problema 3?</strong>, y as sucesivamente el orden se establece en las filas, de superior a inferior. Para el valor a asignarse puede usar la siguiente escala:
