    #t_2_7.titulo-segundo.color-acento-contenido
      h2(data-aos="fade-left") 2.7 Anlisis causa-efecto a travs de la "metodologa de rboles"

    .row.my-5.align-items-center(data-aos="fade-right")
      .col-lg-4
        figure
          img(src="@/assets/curso/temas/tema2/img61.svg", alt="")
      .col-lg-8
        .cajon.color-secundario.p-4(data-aos="slide-down",style="background: #DEEBAD")
          p.mt-0 El problema central ya definido anteriormente es el <strong>punto de partida del anlisis a efectuar con la herramienta de rboles</strong>, aunque existan otras herramientas de anlisis causa-efecto como el mtodo del <strong>diagrama de flechas (ADM)</strong> el <strong>mtodo de la ruta crtica (CPM)</strong> el <strong>mtodo del diagrama de precedencias (PDM) la tcnica de revisin y evaluacin de programas (PERT)</strong> que son mtodos complejos usados sobre todo en anlisis en el contexto de la industria; adems, hay otros ms amigables como pueden ser el <strong>diagrama de Pareto</strong> o el <strong>diagrama de Ishikawa (espina de pescado)</strong>; de todos estos el <strong>mtodo de rboles</strong> es el que resulta, en efecto, ms conveniente para su aplicacin en el EML. 
      
    .row.my-4(data-aos="fade-right")
      .col-lg-6
        p Es importante tener en cuenta que <strong>no hay un problema central definitivo o absoluto</strong> sino que depende de las capacidades y recursos del proyecto. A mayor capacidad de accin, mayor es el reto que se puede asumir y ms complejo puede ser el problema central. Lo anterior significa que <strong>no existe una manera nica de abordar una problemtica</strong> y por tanto, existen <strong>varias formas de solucionarla, distintos niveles en los que se puede actuar y un espectro ms o menos amplio en la cobertura o alcance del proyecto.</strong> Precisamente una bondad del <strong>EML</strong> radica en tener claridad a travs de este anlisis de cmo resulta ms efectiva una intervencin; esto es ms eficaz y ms eficiente en relacin siempre a la capacidad de solucionarlo.
      .col-lg-6
        p Tambin podemos resumir este aporte del mtodo a <strong>una mejor manera de organizar la informacin</strong> obtenida en un momento dado para lograr orientar de manera efectiva los recursos y esfuerzos hacia un objetivo claro que nos permita obtener el mayor beneficio e impacto posibles. El rbol de problemas es, bajo el <strong>EML</strong>, la herramienta que ayuda a identificar el problema central, pero debido a que el uso de la herramienta no es fcil de abordar desde el contexto de la ruralidad, los anteriores pasos son la forma de <strong>producir insumos de calidad</strong> (anlisis de participacin de actores, identificacin y definicin previa de problemas, etc.) para el uso adecuado de esta herramienta clave.

    .container.tarjeta.tarjeta--blanca.p-4.p-md-5

      .tarjeta.tarjeta--gris.p-4
        PasosB.color-acento-contenido
          .row.justify-content-center(titulo="")
            .col-md-10
